import React from "react";
import { useSelector } from "react-redux";
import { selectProducts } from "../../redux/selectors/products";
import Product from "../Product/Product";
import NotFound from "./NotFound";
import Pagination from "./Pagination";

const mapState = (state) => ({
	products: selectProducts(state),
	prev: state.products.prev,
	next: state.products.next,
	count: state.products.count,
});

function ProductsMap({ setClickedLoadMore, setLoadMore, isLoading }) {
	const { products, next, count } = useSelector(mapState);
	return (
		<>
			{count >= 1 && products.length >= 1 && (
				<>
					<div className="row  row-cols-2 row-cols-sm-3 row-cols-md-5 my-4">
						{Array.isArray(products) &&
							products.map((product) => {
								return (
									<Product
										key={`${product.id}${product.name}`}
										cls="mt-md-4"
										item={product}
									/>
								);
							})}
					</div>

					{next && (
						<Pagination
							setClickedLoadMore={setClickedLoadMore}
							setLoadMore={setLoadMore}
							productsLength={products.length}
							isLoading={isLoading}
						/>
					)}
				</>
			)}
			{count < 1 && <NotFound />}
		</>
	);
}

export default ProductsMap;
